import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { TeamsProvider } from '../../providers/teams/teams';
import { TeamDataPage } from '../team-data/team-data';

@IonicPage()
@Component({
  selector: 'page-teams',
  templateUrl: 'teams.html',
})
export class TeamsPage {

  teams:any;

  constructor(public navCtrl: NavController, public navParams: NavParams, private teamsProvider: TeamsProvider) {
  }

  addTeam() {
    alert("Success");
  }

  itemSelected(item) {
    this.navCtrl.push(TeamDataPage, item);
  }

  ionViewDidLoad() {
    this.teamsProvider.getTeams().subscribe(data => {
      this.teams = data.colors;
    })
  }

}



